.navbar-default {
    background-color: transparent;
    border-color: rgba(white, .3);
    @include sans-serif-font;
    @include transition-all;
    
    .navbar-header .navbar-brand {
        color: rgba(white, .7);
        @include sans-serif-font;
        font-weight: 700;
        text-transform: uppercase;
        
        &:hover, &:focus {
            color: white;
        }
    }

    .nav {
        > li {
            > a, > a:focus {
                text-transform: uppercase;
                font-weight: 700;
                font-size: 13px;
                color: rgba(white, .7);

                &:hover {
                    color: white;
                }
            }
            
            &.active {
                > a, > a:focus {
                    color: white !important;
                    background-color: transparent;

                    &:hover {
                        background-color: transparent;
                    }
                }
            }
        }
    }

    &.affix {
        background-color: white;
        border-color: rgba($theme-dark, .5);
        
        .navbar-header .navbar-brand {
            color: $theme-primary;
            font-size: 14px;
            
            &:hover, &:focus {
                color: darken($theme-primary, 10%);
            }
        }

        .nav > li > a, .nav > li > a:focus {
            color: $theme-dark;

            &:hover {
                color: $theme-primary;
            }
        }
    }
}


// Homepage Header

header {
    position: relative;
    width: 100%;
    min-height: auto;
    @include background-cover;
    background-position: center;
    background-image: url('../img/andrew-kliatskyi-iQUf7gndqXE-unsplash.jpg');
    text-align: center;
    color: rgb(230, 230, 230);
    .header-content {
        position: relative;
        text-align: center;
        padding: 100px 15px 100px;
        width: 100%;
        .header-content-inner {
            h1 {
                font-weight: 700;
                text-transform: none;
                margin-top: 0;
                margin-bottom: 0;
            }
            hr {
                margin: 30px auto;
            }
            p {
                font-weight: 300;
                color: rgba(rgb(230, 230, 230), .9);
                font-size: 16px;
                margin-bottom: 50px;
            }
        }
    }
    @media (min-width: 768px) {
        min-height: 100%;
        .header-content {
            position: absolute;
            top: 50%;
            -webkit-transform: translateY(-50%);
            -ms-transform: translateY(-50%);
            transform: translateY(-50%);
            padding: 0 50px;
            .header-content-inner {
                max-width: 1000px;
                margin-left: auto;
                margin-right: auto;
                p {
                    font-size: 18px;
                    max-width: 80%;
                    margin-left: auto;
                    margin-right: auto;
                }
            }
        }
    }
}

// Sections

.section-heading {
    margin-top: 0;
}

.service-box {
    max-width: 400px;
    margin: 50px auto 0;
    @media (min-width: 992px) {
        margin: 20px auto 0;
    }
    p {
        margin-bottom: 0;
    }
}

.full-screen-container {
    width: 100%;
    height: 100vh; /* This ensures it takes the full height of the viewport */
    display: flex;
    flex-direction: column;
    justify-content: center; /* This centers the content inside the container vertically */
    align-items: center; /* This centers the content inside the container horizontally */
}

// Inserted new portfolio-box from GPT
.portfolio-box {
    position: relative;
    display: block;
    margin: 20rem;
    .portfolio-box-caption {
        color: white;
        opacity: 0;
        display: block;
        background: rgba($theme-primary, .9);
        position: absolute;
        bottom: 0;
        text-align: center;
        width: 100%;
        height: 100%;
        @include transition-all;
        .portfolio-box-caption-content {
            width: 100%;
            text-align: center;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            .project-category,
            .project-name {
                @include sans-serif-font;
                padding: 0 15px;
            }
            .project-category {
                text-transform: uppercase;
                font-weight: 600;
                font-size: 14px;
            }
            .project-name {
                font-size: 18px;
            }
        }
    }
    &:hover {
        .portfolio-box-caption {
            opacity: 1;
        }
    }
    @media (min-width: 768px) {
        .portfolio-box-caption {
            .portfolio-box-caption-content {
                .project-category {
                    font-size: 16px;
                }
                .project-name {
                    font-size: 22px;
                }
            }
        }
    }
}

/* For your images */
.img-responsive {
    max-width: 100%; /* Makes sure the image doesn't exceed the container */
    height: auto; /* Allows the image to keep its natural aspect ratio */
}

.portfolio-image {
    width: 100%;     /* Takes the full width of the container */
    height: auto;    /* Keeps the image's natural aspect ratio */
    object-fit: cover; /* Covers the entire space without distortion */
    max-height: 600px; /* Set a maximum height if desired to control the height */
}


// call-to-action
.call-to-action {
    h2 {
        margin: 0 auto 20px;
    }
}

.custom-list-container {
    text-align: center;
}

