.custom-list {
    list-style: disc;
    padding-left: 8em;  /* Use this value for larger screens */
    margin-left: 0;
    display: inline-block;
    text-align: left;
}

@media (max-width: 768px) {
    .custom-list {
        padding-left: 1em;  /* Adjusted value for smaller screens */
    }
}


.custom-list li {
    margin-bottom: 16px;
    color: #9d9d9d;
    font-size: 1.3em;
}


// Bootstrap Overrides
.text-primary {
    color: $theme-primary;
}

.no-gutter > [class*='col-'] {
    padding-right:0;
    padding-left:0;
}

.small-icon {
    margin-bottom: 15px; /* Adjust this value as needed */
}

// Button Styles
.btn-default {
    @include button-variant($theme-dark, white, white);
}

.btn-primary {
    @include button-variant(white, $theme-primary, $theme-primary);
}

.btn {
    @include sans-serif-font;
    border: none;
    border-radius: 300px;
    font-weight: 700;
    text-transform: uppercase;
}

.btn-xl {
    padding: 15px 30px;
}

// Contact
#contact .fa {
    color: $theme-dark;
    font-size: 4em;
}

// Extras
// -- Highlight Color Customization
::-moz-selection {
    color: white;
    text-shadow: none;
    background: $theme-dark;
}

::selection {
    color: white;
    text-shadow: none;
    background: $theme-dark;
}

img::selection {
    color: white;
    background: transparent;
}

img::-moz-selection {
    color: white;
    background: transparent;
}

body {
    -webkit-tap-highlight-color: $theme-dark;
}

